import { Day } from '../types/Day';
import { Language } from '../types/Setting';

export function getDayName(name: Day, lang: Language): string {
  switch (name) {
    case 'sun':
      return lang === 'en' ? name : '';
    case 'mon':
      return lang === 'en' ? name : '';
    case 'tus':
      return lang === 'en' ? name : '';
    case 'wed':
      return lang === 'en' ? name : '';
    case 'thu':
      return lang === 'en' ? name : '';
    case 'fri':
      return lang === 'en' ? name : '';
    case 'sut':
      return lang === 'en' ? name : '';
    default:
      return lang === 'en' ? name : '';
  }
};
