import { Language } from "../types/Setting";

export const getStringMonth = (month: number, lang: Language): string => {
  switch (month) {
    case 0:
      return lang === 'en' ? 'January' : '';
    case 1:
      return lang === 'en' ? 'February' : '';
    case 2:
      return lang === 'en' ? 'March' : '';
    case 3:
      return lang === 'en' ? 'April' : '';
    case 4:
      return lang === 'en' ? 'May' : '';
    case 5:
      return lang === 'en' ? 'June' : '';
    case 6:
      return lang === 'en' ? 'July' : '';
    case 7:
      return lang === 'en' ? 'August' : '';
    case 8:
      return lang === 'en' ? 'September' : '';
    case 9:
      return lang === 'en' ? 'October' : '';
    case 10:
      return lang === 'en' ? 'November' : '';
    case 11:
      return lang === 'en' ? 'December' : '';
    default:
      return lang === 'en' ? 'January' : '';
  }
};
