import { Language } from '../types/Setting';

export function getStringDay(day: number, lang: Language): string {
  switch (day) {
    case 0:
      return lang === 'en' ? 'Sunday' : '';
    case 1:
      return lang === 'en' ? 'Monday' : '';
    case 2:
      return lang === 'en' ? 'Tuesday' : '';
    case 3:
      return lang === 'en' ? 'Wednesday' : '';
    case 4:
      return lang === 'en' ? 'Thursday' : '';
    case 5:
      return lang === 'en' ? 'Friday' : '\'';
    case 6:
      return lang === 'en' ? 'Saturday' : '';
    default:
      return lang === 'en' ? 'Sunday' : '';
  }
};
