import { Language } from "../types/Setting";

export const getStringMonthPart = (month: number, lang: Language): string => {
  switch (month) {
    case 0:
      return lang === 'en' ? 'Jan' : '';
    case 1:
      return lang === 'en' ? 'Feb' : '';
    case 2:
      return lang === 'en' ? 'Mar' : '';
    case 3:
      return lang === 'en' ? 'Apr' : '';
    case 4:
      return lang === 'en' ? 'May' : '';
    case 5:
      return lang === 'en' ? 'Jun' : '';
    case 6:
      return lang === 'en' ? 'Jul' : '';
    case 7:
      return lang === 'en' ? 'Aug' : '';
    case 8:
      return lang === 'en' ? 'Sep' : '';
    case 9:
      return lang === 'en' ? 'Oct' : '';
    case 10:
      return lang === 'en' ? 'Nov' : '';
    case 11:
      return lang === 'en' ? 'Dec' : '';
    default:
      return lang === 'en' ? 'Jan' : '';
  }
};
